import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { TakeSurveyComponent } from './take.survey.component';
import { TakePreviewComponent } from './take.preview/take.preview.component';

const surveyRoutes: Routes = [
    {
      path: 'survey/preview/:id',
      component: TakePreviewComponent,
    },
    {
      path: ':key/:id',
      component: TakeSurveyComponent,
    }
];

@NgModule({
  imports: [
    RouterModule.forChild(surveyRoutes)
  ],
  exports: [
    RouterModule
  ]
})
export class TakeSurveyRoutesModule { }



