  ngAfterViewInit():void{
    setTimeout(() => {
      let elems = document.getElementsByClassName('imageZoomS2c');
      // console.log("elements",elems)
      for(let i=0; i< elems.length; i++){
        // console.log("elems[i]",elems[i])
        elems[i].addEventListener("wheel", function(event){
          // console.log("this",this)
          // console.log("eve",event)
          // event = window[''] || event; // old IE support
          let delta = Math.max(-1, Math.min(1, (event['wheelDelta'] || event['detail'])));
          // console.log("delta",delta)
          if(delta > 0) {
            // console.log('mouse wheel up');
            (<HTMLElement>this).style.width = (parseInt((<HTMLElement>this).style.width, 10)+10)+"px";
            (<HTMLElement>this).style.height = (parseInt((<HTMLElement>this).style.height, 10)+10)+"px";
            (<HTMLElement>this).style['background-size'] =  (parseInt((<HTMLElement>this).style.width, 10)+10)+"px"+" "+(parseInt((<HTMLElement>this).style.height, 10)+10)+"px";
          } else if(delta < 0) {
            console.log('mouse wheel down');
            (<HTMLElement>this).style.width = (parseInt((<HTMLElement>this).style.width, 10)-10)+"px";
            (<HTMLElement>this).style.height = (parseInt((<HTMLElement>this).style.height, 10)-10)+"px";
            (<HTMLElement>this).style['background-size'] =  (parseInt((<HTMLElement>this).style.width, 10)-10)+"px"+" "+(parseInt((<HTMLElement>this).style.height, 10)-10)+"px";
          }
          // for IE
          event.returnValue = false;
          // for Chrome and Firefox
          if(event.preventDefault) {
              event.preventDefault();
          }
        });
      }
    }, 10000);
  }

