import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { BsDropdownModule } from 'ngx-bootstrap/dropdown';
import {SlimLoadingBarService, SlimLoadingBarModule} from 'ng2-slim-loading-bar';


import { SharedModule } from '../../shared/shared.module';
import { TakeSurveyRoutesModule } from './take.survey.routes.module';

import { TakePreviewComponent } from './take.preview/take.preview.component';
import { TakeSurveyComponent } from './take.survey.component';
import { PageComponent } from './page/page.component';
import { QuestionComponent } from './question/question.component';

import { TakeSurveyService } from './services/take.survey.service';
import { SurveyOptionsService } from './services/survey.options.service';
import { SurveyResponseService } from './services/survey.response.service';
import { EditDataService } from '../survey/edit/edit.data.service';
import { EditConstantsService } from '../survey/edit/edit.constants.service';
import { CustomEndPageComponent } from './custom.end.page/custom.end.page.component';

@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    TakeSurveyRoutesModule,
    BsDropdownModule.forRoot(),
    SlimLoadingBarModule.forRoot()
  ],
  declarations: [
    TakePreviewComponent,
  	TakeSurveyComponent,
    PageComponent,
    QuestionComponent,
    CustomEndPageComponent
  ],
  providers: [
    TakeSurveyService,
    SurveyOptionsService,
    SurveyResponseService,
  	EditDataService,
    EditConstantsService,
    SlimLoadingBarService
  ]
})
export class TakeSurveyModule { }



